import { graphql } from 'gatsby';
import React from 'react';
import { Trans } from 'react-i18next';
import { Link, withIntl } from '../../i18n';
import PageWrapper from '../../components/PageWrapper';

const WorkPage = ({ t, ...context }) => {
  const { name } = context.pageContext.page.artist;

  return (
    <PageWrapper title={t('home.title')} {...context}>
      {name}
    </PageWrapper>
  )
};

export const query = graphql`
  query WorkQuery($path: String!) {
    sitePage(path: { eq: $path }) {
      context {
        locale
        page {
          artist {
            key
            name
            preposition
            lastname
            works {
              key
            }
          }
          work {
            key
            available
            date
            images
            title
            description
            width
            depth
            height
            medium
            surface
          }
          previous {
            key
            title
          }
          next {
            key
            title
          }
        }
      }
    }
  }
`;


export default withIntl()(WorkPage);
